/* Common button styles */
.button {
	float: left;
	min-width: 160px;
	max-width: 260px;
    height : 67px;
	display: block;
	margin: 1em;
	padding: 1em 2em;
	border-color: black;
	background: none;
	color: inherit;
	vertical-align: middle;
	z-index: 1;
	-webkit-backface-visibility: hidden;
	-moz-osx-font-smoothing: grayscale;
}
.button:focus {
	outline: none;
}
.button > span {
	vertical-align: middle;
}

/* Text color adjustments (we could stick to the "inherit" but that does not work well in Safari) */
.bg-3 .button {
	color: #fff;
	border-color: black;
}

/* Sizes
.button--size-s {
	font-size: 14px;
}
.button--size-m {
	font-size: 16px;
}
.button--size-l {
	font-size: 18px;
} */

/* Typography and Roundedness */
.button--text-upper {
	letter-spacing: 2px;
	text-transform: uppercase;
}
/* 
.button--text-thin {
	font-weight: 300;
}

.button--text-medium {
	font-weight: 500;
}*/
.button--text-thick {
	font-size: 18px;
} 
.button--round-s {
	border-radius: 5px;
}
.button--round-m {
	border-radius: 15px;
}
.button--round-l {
	border-radius: 40px;
    border-width: 2px;
    border-color: black;
}

/* Borders */
.button--border-thin {
	border: 1px solid;
}
.button--border-medium {
	border: 2px solid;
}
.button--border-thick {
	border: 3px solid;
}

/* here */
.button.button--wapasha {
	background: green;
	color: #fff;
	transition: background-color 0.3s, color 0.3s;
}
/* here */
.button--wapasha.button--inverted {
	background: #fff;
	color: black;
    border-width: 1.1px;
    border-color: black;
}
