@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&family=Source+Sans+Pro:wght@300&display=swap');
.main
{
    
    font-family: 'Source Sans Pro', sans-serif;
    text-align:center;
    margin: 0;
    padding: 50;
    min-height: 100vh;
    background: linear-gradient(141deg, #00E0FF 0%, #74F9FF  51%, #A6FFF2 75%);
}
.main2
{
    padding-top: 80px;
}
.headingone
{
    text-align:center;
    float : center;
    align-items: center;
    font-size: 70px;
    font-weight: 400;
}
.feat{

    padding-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: consolas;
}

.container0{
    width: 1000px;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.container0 .card0{
    position: relative;
    cursor: pointer;
}

.container0 .card0 .face{
    width: 300px;
    height: 200px;
    transition: 0.5s;
}

.container0 .card0 .face.face1{
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
}

.container0 .card0:hover .face.face1{
    background: #E93B81;
    transform: translateY(0);
}

.container0 .card0 .face.face1 .content{
    opacity: 0.2;
    transition: 0.5s;
}

.container0 .card0:hover .face.face1 .content{
    opacity: 1;
}

.container0 .card0 .face.face1 .content img{
    max-width: 100px;
}

.container0 .card0 .face.face1 .content h3{
    margin: 10px 0 0;
    padding: 0;
    color: white;
    text-align: center;
    font-size: 1.5em;
}

.container0 .card0 .face.face2{
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    transform: translateY(-100px);
}

.container0 .card0:hover .face.face2{
    transform: translateY(0);
}

.container0 .card0 .face.face2 .content p{
    margin: 0;
    padding: 0;
}

.container0 .card0 .face.face2 .content a{
    margin: 15px 0 0;
    display:  inline-block;
    text-decoration: none;
    font-weight: 900;
    color: black;
    padding: 5px;
    border: 1px solid #333;
}

.container0 .card0 .face.face2 .content a:hover{
    background: black;
    color: #fff;
}